import React, {useState, ChangeEvent} from 'react';
import axios from 'axios';

//import {Review} from "../../type";

const ReviewImport = () => {

	const [modelname, setModelname] = useState('xx');
	const [reviewFile, setReviewFile] = useState([]);
				
	const config = {
		headers: {
			'Content-Type': 'multipart/form-data'
		}
	};
	
	//submit to backend
    const review_posttobackend = (event) => {
		console.log(modelname);
		console.log(reviewFile);
		
		event.preventDefault()
		
		let dataArray = new FormData();
		dataArray.append("modelname",modelname);
		dataArray.append("reviewFile",reviewFile);
		
		//axios call
        axios.post(`${process.env.REACT_APP_API_URL}/api/reviews/upload/`, 
					//{modelname: modelname, reviewFile: reviewFile}, 
					dataArray,
					config
				)
        .then(res => {
			console.log('posted data')
        })
        .catch(err => {
			console.log('data error');
        })
    };
	
	const csrf = async () => {
		const response = await axios.get('/getCSRFToken');
		
		axios.defaults.headers.post['X-CSRF-Token'] = response.data.CSRFToken ;
	};
	
    return (
       <div>
		   Data Page1
		
			<label> Reviews File </label>
			<form onSubmit={review_posttobackend}>
				<input type="file" name="reviewFile" onChange={e => setReviewFile(e.target.files[0])}/>
				<label> model name </label>
				<input type="text" name="modelname" onChange={e => setModelname(e.target.value)}/>
				
				<button type="submit" >Get Data</button>
			</form>	
	   </div>
    );
};


export default ReviewImport;
